import {requesthome,requestdetail} from "./request";

export function gethomedata(){
    return requesthome({
        url:'/home/multidata'
    })
}
export function gethomedatalist(type,page){
    return requestdetail({
        url:'/api/w6/home/data',
        params:{
            type,
            page
        }
    })
}

export function getdetail(iid){
    return requestdetail({
        url:'/api/w6/detail',
        params:{
            iid
        }
    })
}

export function getrecommend() {
    return requestdetail({
        url:'/api/w6/recommend',
    })
}