import axios from 'axios';

export function requesthome(config){
    //创建axios实例
    const instan = axios.create({
        baseURL:'http://123.207.32.32:8000',
        timeout:2000
    })

    //axios的拦截器
    // 请求拦截
    instan.interceptors.request.use(config =>{
        return config
    },err =>{
        console.log(err);
    })

    //响应拦截
    instan.interceptors.response.use(res =>{
        return res.data
        // console.log(res.data);
    },err =>{
        console.log(err);
    })
    
    //发送请求 promise
    return instan(config)  
}
